import React from "react";
import { Switch, Route } from "react-router-dom";
import { Row, Col } from "react-bootstrap";

import HeaderApp from "../components/HeaderApp";
import MenuApp from "../components/MenuApp";

import Dashboard from "../pages/DashBoard";
import Verbas from "../pages/Verbas";
import OrdensServico from "../pages/OrdensServico";
import OrdensCompra from "../pages/OrdensCompra";
import Especificacoes from "../pages/Especificacoes";
import Relatorios from "../pages/Relatorios";

const style = {
  menu: {
    backgroundColor: "#F8F9FA",
    padding: "5px 20px",
    height: "90vh"
  }
};

const AppHome = props => (
  <div style={{ padding: "0px", margin: "0px" }}>
    <HeaderApp />
    <Row style={{ padding: "0px", margin: "0px" }}>
      <Col xs={2} style={style.menu}>
        <MenuApp />
      </Col>
      <Col xs={10} style={{ borderLeft: "2px solid gray" }}>
        <Switch>
          <Route path="/app/dashboard" component={Dashboard} />
          <Route path="/app/verbas" component={Verbas} />
          <Route path="/app/oss" component={OrdensServico} />
          <Route path="/app/ocs" component={OrdensCompra} />
          <Route path="/app/especificacoes" component={Especificacoes} />
          <Route path="/app/relatorios" component={Relatorios} />
        </Switch>
      </Col>
    </Row>
  </div>
);

export default AppHome;
